.w-nav-menu {
    position: relative;
    text-align: right;
    width: 100%;
    padding-right: 10px;
}
.nav-link.w--current {
    color: #ff494e;
}
.nav-link.w--current {
    color: #ff494e;
}
.nav-link {
    font-family: Orbitron, sans-serif;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}
.nav-link:hover {
    color: #03bdb8;
}
.w-nav-link {
    position: relative;
    display: inline-block;
    vertical-align: top;
    text-decoration: none;
    color: #ffffff;
    padding: 0 10px;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
}
.artimage {
    width: 60%;
}
@media only screen and (max-width: 600px) {
    .artimage {
        width: 100%;
    }
}
